@import url(https://fonts.googleapis.com/css?family=Roboto:400,700i);
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #000000;
  min-width: 320px;
}

* {
  margin: 0;
  padding: 0;
  list-style: none;
  line-height: 1;
  font-family: "Roboto", sans-serif;
}
.wrap {
  width: 100%;
  margin: auto;
}
.wrap :after {
    content: "";
    display: block;
    height: 0%;
    width: 100%;
    clear: both;
}
.wrap .header {
    color: #50FF44;
    font-family: "Roboto", sans-serif;
    font-style: italic;
    font-weight: bold;
    background-color: #000000;
    font-size: 42px;
    padding: 12px;
}
.wrap .content {
    padding: 3% 15%;
}
.wrap .content .item {
      position: relative;
      float: left;
}
.wrap .content .comicPic {
      width: 50%;
}
.wrap .content .comicPic img {
        border: black solid 5px;
        width: auto;
        height: auto;
        vertical-align: middle;
        max-width: 100%;
        max-height: 100%;
}
.wrap .content .comicInfo {
      width: 50%;
}
.wrap .content .comicInfo .title {
        color: #ffffff;
        background-color: #000000;
        font-family: "Roboto", sans-serif;
        font-weight: bold;
        font-size: 50px;
        padding: 5px;
        margin-left: 5px;
}
.wrap .content .comicInfo .info {
        padding: 30px;
        text-align: left;
        font-size: 22px;
        color: #000000;
}
.wrap .content .comicInfo .info .detail {
          display: table;
          margin-bottom: 20%;
          border-spacing: 13px;
}
.wrap .content .comicInfo .info .detail .data {
            display: table-row;
}
.wrap .content .comicInfo .info .detail .data .col1 {
              display: table-cell;
              font-weight: bold;
              padding-right: 5%;
}
.wrap .content .comicInfo .info .detail .data .col2 {
              display: table-cell;
}
.wrap .content .comicInfo .summary .sumTitle {
        font-weight: bold;
        margin-bottom: 15px;
}
.wrap .content .chapters {
      width: 100%;
      text-align: left;
      margin-top: 5%;
}
.wrap .content .chapters .chTitle {
        background-color: #000000;
        color: #ffffff;
        font-size: 28px;
        font-weight: bold;
        width: 18%;
        padding: 10px;
}
.wrap .content .chapters .chContent {
        border: black solid 3px;
        color: #000000;
        font-size: 18px;
        font-weight: bold;
}
.wrap .content .chapters .chContent .chDetail {
          padding: 12px;
          cursor: pointer;
}
.wrap .content .chapters .chContent .chDetail:hover {
            background-color: #000000;
            color: #ffffff;
}
.wrap .content .chapters .chContent .chDetail .new {
            background-color: #50FF44;
            color: #000000;
}
@media screen and (min-width: 400px) and (max-width: 767px) {
.wrap {
    font-size: 12px;
}
.wrap .content .comicInfo {
      width: 100%;
}
.wrap .content .comicInfo .info .detail {
        margin-bottom: 5%;
}
.wrap .content .comicPic {
      width: 100%;
}
.wrap .content .chapters .chTitle {
      width: 33%;
}
}
@media screen and (max-width: 400px) {
.wrap {
    font-size: 12px;
}
.wrap .content .comicInfo {
      width: 100%;
}
.wrap .content .comicInfo .info {
        padding: 20px;
        font-size: 14px;
}
.wrap .content .comicInfo .info .detail {
          margin-bottom: 2%;
}
.wrap .content .comicInfo .title {
        font-size: 32px;
}
.wrap .content .comicPic {
      width: 100%;
}
.wrap .content .chapters .chTitle {
      font-size: 16px;
      width: 40%;
}
.wrap .content .chapters .chContent {
      font-size: 14px;
}
}









